.open-popup {
  color: #000;
  width: 300px;
  background-color: azure;
  border: 1px solid orange;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  margin: 30px;
  padding: 30px 2px;
  font-size: 30px;
  text-decoration: none;
  display: flex;
}

.popup-text {
  margin-bottom: 4px;
  font-size: 18px;
  font-weight: 600;
}

.form-input {
  width: 100%;
  box-sizing: border-box;
  border-color: rgba(5, 5, 5, .2);
  border-radius: 15px;
  margin-bottom: 16px;
  padding: 14px 0 14px 18px;
  font-size: 16px;
  display: block;
}

.form-input:focus {
  outline-color: #9bb537;
}

.submit-btn {
  cursor: pointer;
  color: #f8f8f8;
  width: 360px;
  height: 48px;
  background-color: #9bb537;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 15px;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  display: flex;
}

.submit-btn:hover {
  color: #000;
  box-shadow: 2px 5px 2px 1px rgba(155, 181, 55, .5);
}

.rating {
  flex-direction: row-reverse;
  justify-content: start;
  align-items: center;
  margin-bottom: 32px;
  line-height: .75;
  display: flex;
}

.rating-sending {
  opacity: .2;
}

.rating.rating-set .rating-active, .rating.rating-set .rating-item {
  cursor: pointer;
}

.rating-body {
  color: #919186;
  position: relative;
}

.rating-body:before {
  content: "★★★★★";
  font-size: 32px;
  display: block;
}

.rating-active {
  width: 80%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.rating-active:before {
  content: "★★★★★";
  width: 100%;
  height: 100%;
  color: #eea10c;
  font-size: 32px;
  position: absolute;
  top: 0;
  left: 0;
}

.rating-items {
  width: 100%;
  height: 100%;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.rating-item {
  height: 100%;
  opacity: 0;
  flex: 0 0 20%;
  margin: 0;
}

.rating-value {
  margin-right: 8px;
  font-size: 20px;
  font-weight: 600;
}

.rating-btn-close {
  cursor: pointer;
  position: absolute;
  top: 10px;
  right: 15px;
}

.backdrop {
  z-index: 100;
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: visible;
  background-color: rgba(49, 47, 47, .475);
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  max-width: 424px;
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 15px;
  padding: 32px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal-close-btn {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 10px;
  right: 10px;
}

.rating-modal-close:hover path {
  stroke: #9bb537;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

